* {
    box-sizing: border-box;
}

body {
    font-family: "Karla", sans-serif;
    margin: 0;
    width: 100%;
    background:  #E5E5E5;
}
.header{
    display: flex;
    align-items: center;
    padding: 1.5em;
    gap:8px;
    width:550px; 
    height:65px;
    color: #FFFFFF;
    margin: 0 auto;
    background: linear-gradient(90deg, #672280 1.18%, #A626D3 100%);
}
.header-logo{
    width: 31.31px;
    height: 26.22px;
}

.header-title{
                font-weight: 700;
                font-size: 1.25rem;
                line-height: 24px;
                font-family: 'Karla', sans-serif;
}
.header-text{
                margin-left: auto;
                font-weight: 500;
                font-size: .75rem;
                line-height: 24px;
               font-family: 'Inter', sans-serif;
            }
            
            
main {
    padding: 36px;
  
}

.form {
    display: grid;
    grid-template: 40px 40px / 1fr 1fr;
    gap: 17px;
}

.form--input {
    font-family: "Karla", sans-serif;
    border-radius: 5px;
    border: 1px solid #D5D4D8;
    text-indent: 5px;
}

.form--button {
    grid-column: 1 / -1;
    font-family: "Karla", sans-serif;
    border-radius: 5px;
    background: linear-gradient(90.41deg, #711F8D 1.14%, #A818DA 100%);
    color: white;
    border: none;
    cursor: pointer;
}

.main-image{
    padding:2em;
    max-width: 100%;
    max-height: 100%;
}

.meme {
    position: relative;
}

.meme--image {
    max-width: 100%;
    width: 100%;
    border-radius: 3px;
    margin-top:2em;
  
}

.meme--text {
    position: absolute;
    width: 80%;
    text-align: center;
    left: 50%;
    transform: translateX(-50%);
    margin:50px 0;
    padding: 0 5px;
    font-family: impact, sans-serif;
    font-size: 2em;
    text-transform: uppercase;
    color: white;
    letter-spacing: 1px;
    text-shadow:
        2px 2px 0 #000,
        -2px -2px 0 #000,
        2px -2px 0 #000,
        -2px 2px 0 #000,
        0 2px 0 #000,
        2px 0 0 #000,
        0 -2px 0 #000,
        -2px 0 0 #000,
        2px 2px 5px #000;
}

.bottom {
    bottom: 0;
}

.top {
    top: 0;
}